/* You can add global styles to this file, and also import other style files */
@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.highlight-name {
  color: #D6526D;
  font-weight: bold;
}

body {
  background-color: #f9f9f9;
  color: #333;
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  padding: 20px;
}

.intro {
  flex: 1;
  text-align: left;
  padding: 0;
  margin: 0; 
}

.home-gallery {
  display: flex;
  justify-content: space-between;
  align-items: center; 
  min-height: 80vh;
  margin: 0; 
  padding: 0px;
  gap: 40px;
}

.gallery {
  flex: 1;
  text-align: left;
}


.gallery .grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.gallery img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h1, h2 {
  color: #333;
}


/* Navbar Styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px ;
  background-color: #f9f9f9;
  top: 0;
  z-index: 1000;
}

.navbar .logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #D6526D;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.nav-links li {
  font-size: 1rem;
}

.nav-links a {
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #D6526D;
}

.hamburger {
  display: none;
  font-size: 2rem;
  cursor: pointer;
}

/* Smooth scrolling for anchor links */
html {
  scroll-behavior: smooth;
}
.experience-section {
  padding: 50px 20px;
  background-color: #f9f9f9;
  color: #333;
  text-align: center;
}

.experience-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.experience-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 20px;
}

.experience-card {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.experience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}

.experience-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 15px;
}

.experience-card h3 {
  font-size: 1.5rem;
  margin-bottom: 5 px;
}

.experience-card h4 {
  font-size: 1.2rem;
  color: #777;
  margin-bottom: 3 px;
}

.experience-card h5 {
  font-size: 13px;
  color: #444;
  margin-bottom: 5 px;
}

.experience-card p {
  font-size: 1rem;
  margin: 0 px;
}

.details-button {
  display: block;
  margin: 15px auto 0;
  background-color: #282c34;
  color: white;
  border: 1px;
  border-color: #282c34;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.details-button:hover {
  background-color: #D6526D;
}



.footer {
  text-align: center;
  padding: 10px 0px ;
}


/* Mobile Styles for small screens (up to 768px) */
@media (max-width: 768px) {
  .App-header {
    font-size: calc(8px + 2vmin);
    padding: 10px;
  }

  .intro {
    flex: 1;
    text-align: center;
    padding: 0;
    margin: 0; 
  }

  .nav-links {
    display: none; 
    flex-direction: column;
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 10px 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }

  .nav-links.show {
    display: flex;
  }

  .hamburger {
    display: block;
  }

  .home-gallery {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .gallery .grid {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .gallery img {
    width: 100%;
    height: auto;
    max-width: 350px; 
    object-fit: cover;
  }

  .gallery img:not(:first-child) {
    display: none;
  }

  .experience-grid {
    grid-template-columns: 1fr;
  }

  .experience-card {
    padding: 15px;
  }

  .details-button {
    width: 100%;
  }
}

/* Extra Small Devices (up to 480px) */
@media (max-width: 480px) {
  .App-logo {
    height: 30vmin;
  }

  .experience-card img {
    height: 100px;
  }

  .navbar .logo {
    font-size: 1.2rem;
  }

  h1, h2 {
    font-size: 1.5rem;
  }
}
